.header {
    width: 100vw;
    height: 40px;
    background-color: blue;
    text-align: right;
}


.header .filter_product {
    width: 220px;
    padding: 4px 8px;
    margin-top: 6px;
    margin-right: 10px;
}

.header .search_input {
    width: 220px;
    padding: 4px 8px;
    margin-right: 40px;
    outline: none;
}

@media screen and (max-width: 768px) {
    .header {
        height: 80px;
        text-align: center;
        display: flex;
        justify-content: center;
        flex-direction: column;
    }
    .header .filter_product {
        padding: 4px 40px;
        margin-right: 0;
    }
    
    .header .search_input {
        margin-right: 0;
    }
  }